.contact-section {
  text-align: center;
  padding: 250px;
}

.contact-section ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

section a {
  color: #e9e36e;
}

@media screen and (width <= 1000px) {
  .contact-section {
    padding: 250px 100px;
  }
}

@media screen and (width <= 600px) {
  .contact-section {
    padding: 250px 50px;
  }
}
